import { Component, Element, h, Host, Prop, State } from '@stencil/core';
import { Environments } from '../../entities/environment';
import windowService from '../../services/window-service';
import { navContentStore } from '../../state/nav-content-store';
import { navStateStore } from '../../state/nav-state-store';
import {
  NavigationMode,
  NavigationModeType,
  NavigationTheme,
  NavigationThemeType,
  VERSION
} from '../../utility/constants';
import { getHTMLElement } from '../../utility/helper';
import animationManager from '../../services/animations/animation-manager';
import { isShopAvailable } from '../../utility/helpers/shop-helper';
import { Application, ApplicationType } from '../../utility/constants/app';

@Component({
  tag: 'phn-wrapper',
  styleUrl: 'wrapper.scss',
  scoped: true
})
export class Wrapper {
  @Prop() locale!: string;
  @Prop() breakpoint!: number;
  @Prop() loadingContent = false;
  @Prop() receivedContent = false;
  @Prop() theme: NavigationThemeType = NavigationTheme.light;
  @Prop() mode: NavigationModeType = NavigationMode.navbar;
  @Prop() app: ApplicationType = Application.default;
  @Prop() displayLogoOnly = false;
  @Prop() limitedWidth = false;

  @State() showCountryRecommender = windowService.redirected;
  @State() countryRecommenderClosed = false;

  @Element() el!: HTMLPhnWrapperElement;

  componentWillLoad(): void {
    this.el.style.setProperty('--scrollbar-width', windowService.getScrollbarWidth());

    animationManager.initialize(this.el);
  }

  render(): HTMLPhnWrapperElement {
    const countryRecommenderContent = navContentStore.state.countryRecommender;
    const showCountryRecommender =
      this.showCountryRecommender && !this.countryRecommenderClosed && !this.loadingContent && this.receivedContent;

    const accessibilityStatement = navContentStore.state.accessibilityStatement;

    return (
      <Host>
        {navStateStore.state.env === Environments.PREVIEW ? (
          <phn-p-text theme="dark" size="x-small" class="preview-badge">
            Header v{VERSION} - PP content
          </phn-p-text>
        ) : null}

        {showCountryRecommender ? (
          <phn-country-recommender
            locale={this.locale}
            content={countryRecommenderContent}
            role="region"
            aria-label="Change recommended Region or Language"
            onBannerClosed={() => {
              this.countryRecommenderClosed = true;
            }}
          />
        ) : null}

        {this.locale === 'en-US' && accessibilityStatement !== null ? (
          <a class="screenreader-only" href={accessibilityStatement.link}>
            {accessibilityStatement.text}
          </a>
        ) : null}

        <phn-level-0
          theme={this.theme}
          mode={this.mode}
          locale={this.locale}
          loadingContent={this.loadingContent}
          receivedContent={this.receivedContent}
          breakpoint={this.breakpoint}
          app={this.app}
          displayLogoOnly={this.displayLogoOnly}
          limitedWidth={this.limitedWidth}
        >
          {isShopAvailable(this.locale, this.app) ? (
            <div slot="contextual_drawer_header">
              <slot name="contextual_drawer_header"></slot>
            </div>
          ) : null}
        </phn-level-0>

        <div
          id="main"
          class="main-content"
          onBlur={(e) => {
            const el = getHTMLElement(e.currentTarget);
            if (el !== null) {
              el.removeAttribute('tabIndex');
            }
          }}
        />
      </Host>
    );
  }
}
